<template>
  <div id="app">
    <AddPersonForm :savePersons="savePersons" />
    <PersonsTable :persons='persons'/>
  </div>
</template>

<script>
import AddPersonForm from './components/AddPersonFormVue.vue'
import PersonsTable from './components/PersonsTableVue.vue'

export default {
  name: 'App',
  data() {
    return {
      persons: []
    }
  },
  components: {
    AddPersonForm, PersonsTable
  },
  mounted() {
      if (localStorage.getItem('persons')) {
          try {
              this.persons = JSON.parse(localStorage.getItem('persons'));
          } catch(e) {
              localStorage.removeItem('persons');
          }
      }
  },
  methods: {
    savePersons(data) {
      this.persons = data.persons
      const parsed = JSON.stringify(this.persons);
      localStorage.setItem('persons', parsed);
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
