<template>
    <div class="container mt-5">
        <div class="col-sm-10 mx-auto">
            <form class="row">
                <div class="col-lg-3">
                    <label for="inputFirstName" class="visually-hidden">Имя</label>
                    <input v-model="firstName" type="text" class="form-control" id="inputFirstName" placeholder="Имя">
                </div>
                <div class="col-lg-3">
                    <label for="inputLastName" class="visually-hidden">Фамилия</label>
                    <input v-model="lastName" type="text" class="form-control" id="inputLastName" placeholder="Фамилия">
                </div>
                <div class="col-lg-3">
                    <label for="inputAge" class="visually-hidden">Возраст</label>
                    <input v-model="age" type="text" class="form-control" id="inputAge" placeholder="Возраст">
                </div>
                <div class="col-lg-3">
                    <button @click="addPerson" type="button" class="btn btn-primary mb-3">Добавить</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
export default {
  name: 'AddPersonForm',
  props: ['savePersons'],
  data() {
      return {
        persons: [],
        firstName: null,
        lastName: null,
        age: null
      }
  },
  methods: {
    addPerson() {
        if ((!this.firstName) || 
        (!this.lastName) || 
        (!this.age)) {
            return;
        }
        if ((this.firstName.trim() == '') ||
        (this.lastName.trim() == '') ||
        (this.age.trim() == '')) {
            return;
        }

        let newPerson = {
            firstName: this.firstName.trim(),
            lastName: this.lastName.trim(),
            age: this.age.trim()
        }

        this.persons.push(newPerson);
        this.firstName = ''
        this.lastName = ''
        this.age = ''
        this.savePersons({
            persons: this.persons
        })
    }
  }
}
</script>

